body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.login-form {
    width: 350px;
}
.login-form-button {
    width: 100%;
}

.login{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width:100%;
    height: 100vh;
    background: #f0f2f5;
    background-image: url('https://images.pexels.com/photos/164686/pexels-photo-164686.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940');
    background-repeat: no-repeat;
    background-position: inherit ;
    background-size: cover;

}

.header {
    height: 44px;
    line-height: 44px;
    margin-bottom: 20px;
}

.logoP {
    height: 44px;
    vertical-align: top;
    margin-right: 16px;
}

.title{
    font-size: 1.3rem;
    font-weight: bold;
}

@media(max-width: 800px){
    .login-form{
        width: 200px;
    }
}
.nav {display: flex;
padding: 0 1%;
justify-content: space-between;
align-items: center;
color: #000;
text-decoration: none;}

.cardDataPrfile{
    width: 100%;
    margin-top:5px
}
.TitleCardProfile{
    font-weight: bold
}
.Btn-Card-ProfileEdit{
    color:black;
    border-color: black;
}
.colum-btn-ProfileEdit{
